import React from 'react';
import type { Controller, RenderEditableProps } from '@ali/4ever-cangjie';
// import { SimpleLazyLoad } from '@ali/4ever-component';
import SnippetManager from './snippetManager';
import type { PluginConfig } from './utils/types';
import { SnippetsPanel } from './components/SnippetsPanel';

export function createRenderEditable(config: PluginConfig = {}, manager: SnippetManager) {
  // TODO: FIXME 引入 SimpleLazyLoad 构建时 ts 检查有问题
  // const LazyLoad = config.lazyload || SimpleLazyLoad;
  // const LazySnippetsPanel = LazyLoad(() =>
  //   import('./components/SnippetsPanel').then(({ SnippetsPanel }) => SnippetsPanel),
  // );

  return (props: RenderEditableProps, controller: Controller, next: Function) => {
    const { trigger, text } = manager.getSnippet();

    return (
      <>
        {next()}
        <SnippetsPanel
          controller={controller}
          manager={manager}
          currentTrigger={trigger}
          currentText={text}
        />
      </>
    )
  };
}
